body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", sans-serif;
  background: #fafafa;
}

.container {
  max-width: 420px;
  margin: auto;
  padding: 16px;
}

h1 {
  text-align: center;
}

.form {
  display: flex;
  gap: 8px;
  margin-bottom: 20px;
}

.form input,
.form select,
.form button {
  flex: 1;
  padding: 8px;
  font-size: 14px;
}

.form button {
  background: #007aff;
  color: white;
  border: none;
  border-radius: 8px;
}

.matrix {
  width: 100%;
  border-collapse: separate;
  border-spacing: 8px;
}

.matrix th {
  text-align: center;
  font-size: 14px;
}

.matrix-cell {
  border-radius: 14px;
  padding: 10px 6px;
  text-align: center;
}

.matrix-cell .count {
  font-size: 18px;
  font-weight: bold;
}

.matrix-cell .avg {
  font-size: 12px;
  opacity: 0.8;
}
